___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 1024px) {
  .menu li {
    margin-right: 5px;
  }
  .survey-description .content-one{
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .navbar .container .menu {
    display: none;
  }
  .navbar .container .menu.aktif {
    display: block;
    position: absolute;
    top: 64px;
    background: #008080;
    padding: 10px 20px;
    right: 0;
    left: 0;
  }
  .navbar .container .menu.aktif li a {
    padding: 10px;
    display: block !important;
  }
  .tombol {
    display: block;
  }
  .welcome-content {
    display: flex;
    flex-direction: column;
  }

  .form-section form {
    width: 100%;
    max-width: 400px;
    min-width: 300px;
}

  .benefits-survey {
    grid-template-columns: 1fr;
  }

  .progress-container {
    width: 70%;
  }
  .footer-top {
    flex-direction: column;
    gap: 30px;
  }

  .footer-top .footer-right {
    width: 100%;
    padding-left: 30px;
  }
}

